import React, { Component } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { CSSTransitionGroup } from "react-transition-group";

import { Home } from "./components/scenes";
//components
import Main from "./components/Main";
import animationStyles from "./animationStyles.less";

class Routes extends Component {
  render() {
    return (
      <Router>
        <Switch>
          <Main>
            <CSSTransitionGroup
              transitionName={{
                enter: animationStyles.Enter,
                enterActive: animationStyles.EnterActive,
                leave: animationStyles.Leave,
                leaveActive: animationStyles.LeaveActive
                // appear: styles.Appear,
                // appearActive: styles.AppearActive
              }}
              transitionEnterTimeout={500}
              transitionLeaveTimeout={300}
              transitionAppear={false}
              transitionAppearTimeout={500}
            >
              <Route
                exact
                path
                to="/"
                render={() => (
                  <Home
                    animationStyle={{
                      left: animationStyles.left,
                      right: animationStyles.right
                    }}
                  />
                )}
              />
            </CSSTransitionGroup>
            <Route to="/contact" render={() => <div />} />
          </Main>
        </Switch>
      </Router>
    );
  }
}

export default Routes;
