import React, { Component } from "react";

import styles from "./styles.less";

class LeftMainButton extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render = () => {
    return (
      <div
        className={styles.leftHomeButton + " " + this.props.animationClass}
      />
    );
  };
}

export default LeftMainButton;
